'use client';

import { useState, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import * as Dialog from '@radix-ui/react-dialog';
import {
  X,
  Zap,
  Search,
  Clock,
  Star,
  Loader2,
  Plus,
  Code2,
  Palette,
  Database,
  Globe,
  Smartphone,
  Settings,
  TrendingUp,
} from 'lucide-react';
import { Button } from '@/components/ui/Button';
import { addSkill } from '@/lib/api/profile';
import type { SkillInput, UserSkill } from '@/types/profile';

interface AddSkillModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSkillAdded: (skill: UserSkill) => void;
  existingSkills: string[]; // To prevent duplicates
}

// Predefined skill categories and suggestions
const skillCategories = [
  {
    name: 'Languages',
    icon: <Code2 className="w-4 h-4" />,
    skills: ['JavaScript', 'TypeScript', 'Python', 'Java', 'C++', 'Go', 'Rust', 'Ruby', 'PHP', 'Swift', 'Kotlin'],
  },
  {
    name: 'Frontend',
    icon: <Palette className="w-4 h-4" />,
    skills: ['React', 'Vue.js', 'Angular', 'Next.js', 'Svelte', 'Tailwind CSS', 'Bootstrap', 'SASS', 'HTML5', 'CSS3'],
  },
  {
    name: 'Backend',
    icon: <Database className="w-4 h-4" />,
    skills: ['Node.js', 'Express.js', 'Django', 'Flask', 'FastAPI', 'Spring Boot', 'NestJS', 'GraphQL', 'REST APIs'],
  },
  {
    name: 'Databases',
    icon: <Database className="w-4 h-4" />,
    skills: ['PostgreSQL', 'MongoDB', 'MySQL', 'Redis', 'Firebase', 'Supabase', 'SQLite', 'Prisma', 'Elasticsearch'],
  },
  {
    name: 'DevOps',
    icon: <Settings className="w-4 h-4" />,
    skills: ['Docker', 'Kubernetes', 'AWS', 'GCP', 'Azure', 'CI/CD', 'GitHub Actions', 'Linux', 'Terraform', 'Nginx'],
  },
  {
    name: 'Mobile',
    icon: <Smartphone className="w-4 h-4" />,
    skills: ['React Native', 'Flutter', 'Swift', 'Kotlin', 'iOS', 'Android', 'Expo', 'Ionic'],
  },
];

const proficiencyLevels = [
  { value: 'Beginner', label: 'Beginner', description: 'Just starting out', color: 'from-blue-500 to-cyan-500' },
  { value: 'Intermediate', label: 'Intermediate', description: '1-2 years experience', color: 'from-green-500 to-emerald-500' },
  { value: 'Advanced', label: 'Advanced', description: '2-4 years experience', color: 'from-orange-500 to-amber-500' },
  { value: 'Expert', label: 'Expert', description: '4+ years experience', color: 'from-purple-500 to-pink-500' },
] as const;

export function AddSkillModal({
  isOpen,
  onClose,
  onSkillAdded,
  existingSkills,
}: AddSkillModalProps) {
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const searchInputRef = useRef<HTMLInputElement>(null);

  const [formData, setFormData] = useState<SkillInput>({
    skillName: '',
    proficiency: 'Intermediate',
    yearsOfExp: 1,
  });

  // Filter skills based on search and category
  const filteredSkills = skillCategories
    .filter((cat) => !selectedCategory || cat.name === selectedCategory)
    .flatMap((cat) => cat.skills)
    .filter(
      (skill) =>
        skill.toLowerCase().includes(searchQuery.toLowerCase()) &&
        !existingSkills.includes(skill)
    );

  const handleSkillSelect = (skillName: string) => {
    setFormData((prev) => ({ ...prev, skillName }));
    setSearchQuery(skillName);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.skillName.trim()) {
      setError('Please select or enter a skill');
      return;
    }

    if (existingSkills.includes(formData.skillName)) {
      setError('You already have this skill');
      return;
    }

    setSaving(true);
    setError(null);

    try {
      const skill = await addSkill(formData);
      onSkillAdded(skill);
      // Reset form
      setFormData({
        skillName: '',
        proficiency: 'Intermediate',
        yearsOfExp: 1,
      });
      setSearchQuery('');
      onClose();
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to add skill');
    } finally {
      setSaving(false);
    }
  };

  const handleClose = () => {
    setFormData({
      skillName: '',
      proficiency: 'Intermediate',
      yearsOfExp: 1,
    });
    setSearchQuery('');
    setSelectedCategory(null);
    setError(null);
    onClose();
  };

  return (
    <Dialog.Root open={isOpen} onOpenChange={handleClose}>
      <Dialog.Portal>
        <AnimatePresence>
          {isOpen && (
            <>
              {/* Backdrop */}
              <Dialog.Overlay asChild>
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                  className="fixed inset-0 bg-black/60 backdrop-blur-md z-50"
                />
              </Dialog.Overlay>

              {/* Modal */}
              <Dialog.Content asChild>
                <motion.div
                  initial={{ opacity: 0, scale: 0.95, y: 20 }}
                  animate={{ opacity: 1, scale: 1, y: 0 }}
                  exit={{ opacity: 0, scale: 0.95, y: 20 }}
                  transition={{ type: 'spring', damping: 25, stiffness: 300 }}
                  className="fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-xl max-h-[90vh] z-50"
                >
                  <div className="relative bg-white/90 dark:bg-neutral-900/90 backdrop-blur-xl rounded-2xl border border-white/20 dark:border-neutral-700/50 shadow-2xl overflow-hidden">
                    {/* Gradient accent */}
                    <div className="absolute top-0 left-0 right-0 h-1 bg-gradient-to-r from-blue-500 via-purple-500 to-pink-500" />

                    {/* Header */}
                    <div className="flex items-center justify-between p-5 border-b border-gray-200/50 dark:border-neutral-800/50">
                      <div className="flex items-center gap-3">
                        <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center shadow-lg">
                          <Zap className="w-5 h-5 text-white" />
                        </div>
                        <Dialog.Title className="text-xl font-bold text-gray-900 dark:text-white">
                          Add Skill
                        </Dialog.Title>
                      </div>
                      <Dialog.Close asChild>
                        <button className="p-2 text-gray-500 dark:text-neutral-400 hover:text-gray-700 dark:hover:text-white transition-colors rounded-lg hover:bg-gray-100 dark:hover:bg-neutral-800">
                          <X className="w-5 h-5" />
                        </button>
                      </Dialog.Close>
                    </div>

                    {/* Form */}
                    <form onSubmit={handleSubmit} className="overflow-y-auto max-h-[calc(90vh-160px)]">
                      <div className="p-5 space-y-5">
                        {/* Error */}
                        {error && (
                          <motion.div
                            initial={{ opacity: 0, y: -10 }}
                            animate={{ opacity: 1, y: 0 }}
                            className="p-3 bg-red-500/10 border border-red-500/20 rounded-xl text-red-600 dark:text-red-400 text-sm"
                          >
                            {error}
                          </motion.div>
                        )}

                        {/* Skill Search */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Search className="w-4 h-4 text-blue-500" />
                            Search or Enter Skill
                          </label>
                          <div className="relative">
                            <input
                              ref={searchInputRef}
                              type="text"
                              value={searchQuery}
                              onChange={(e) => {
                                setSearchQuery(e.target.value);
                                setFormData((prev) => ({ ...prev, skillName: e.target.value }));
                              }}
                              placeholder="e.g., React, Python, Docker..."
                              className="w-full px-4 py-3 pl-11 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500 transition-all"
                            />
                            <Search className="absolute left-4 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
                          </div>
                        </div>

                        {/* Category Filter */}
                        <div className="space-y-2">
                          <label className="text-sm font-medium text-gray-700 dark:text-neutral-300">
                            Browse by Category
                          </label>
                          <div className="flex flex-wrap gap-2">
                            {skillCategories.map((cat) => (
                              <button
                                key={cat.name}
                                type="button"
                                onClick={() =>
                                  setSelectedCategory(
                                    selectedCategory === cat.name ? null : cat.name
                                  )
                                }
                                className={`flex items-center gap-1.5 px-3 py-1.5 rounded-lg text-sm transition-all ${
                                  selectedCategory === cat.name
                                    ? 'bg-blue-500 text-white shadow-lg'
                                    : 'bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 hover:bg-gray-200 dark:hover:bg-neutral-700'
                                }`}
                              >
                                {cat.icon}
                                {cat.name}
                              </button>
                            ))}
                          </div>
                        </div>

                        {/* Skill Suggestions */}
                        {(searchQuery || selectedCategory) && filteredSkills.length > 0 && (
                          <div className="space-y-2">
                            <label className="text-sm font-medium text-gray-700 dark:text-neutral-300">
                              Suggestions
                            </label>
                            <div className="flex flex-wrap gap-2 max-h-32 overflow-y-auto p-2 bg-gray-50 dark:bg-neutral-800/30 rounded-xl">
                              {filteredSkills.slice(0, 15).map((skill) => (
                                <motion.button
                                  key={skill}
                                  type="button"
                                  initial={{ scale: 0.9, opacity: 0 }}
                                  animate={{ scale: 1, opacity: 1 }}
                                  onClick={() => handleSkillSelect(skill)}
                                  className={`px-3 py-1.5 rounded-lg text-sm font-medium transition-all ${
                                    formData.skillName === skill
                                      ? 'bg-blue-500 text-white shadow-lg'
                                      : 'bg-white dark:bg-neutral-700 text-gray-700 dark:text-neutral-300 hover:bg-blue-100 dark:hover:bg-neutral-600 border border-gray-200 dark:border-neutral-600'
                                  }`}
                                >
                                  {skill}
                                </motion.button>
                              ))}
                            </div>
                          </div>
                        )}

                        {/* Proficiency Level */}
                        <div className="space-y-3">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <TrendingUp className="w-4 h-4 text-blue-500" />
                            Proficiency Level
                          </label>
                          <div className="grid grid-cols-2 gap-3">
                            {proficiencyLevels.map((level) => (
                              <button
                                key={level.value}
                                type="button"
                                onClick={() =>
                                  setFormData((prev) => ({ ...prev, proficiency: level.value }))
                                }
                                className={`relative p-3 rounded-xl border-2 transition-all ${
                                  formData.proficiency === level.value
                                    ? 'border-blue-500 bg-blue-50 dark:bg-blue-500/10'
                                    : 'border-gray-200 dark:border-neutral-700 hover:border-gray-300 dark:hover:border-neutral-600'
                                }`}
                              >
                                <div className="flex items-center gap-2">
                                  <div
                                    className={`w-3 h-3 rounded-full bg-gradient-to-r ${level.color}`}
                                  />
                                  <span className="font-medium text-gray-900 dark:text-white text-sm">
                                    {level.label}
                                  </span>
                                </div>
                                <p className="text-xs text-gray-500 dark:text-neutral-400 mt-1 text-left">
                                  {level.description}
                                </p>
                                {formData.proficiency === level.value && (
                                  <motion.div
                                    layoutId="proficiency-check"
                                    className="absolute top-2 right-2"
                                  >
                                    <Star className="w-4 h-4 text-blue-500 fill-blue-500" />
                                  </motion.div>
                                )}
                              </button>
                            ))}
                          </div>
                        </div>

                        {/* Years of Experience */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Clock className="w-4 h-4 text-blue-500" />
                            Years of Experience
                          </label>
                          <div className="flex items-center gap-4">
                            <input
                              type="range"
                              min="0"
                              max="15"
                              step="0.5"
                              value={formData.yearsOfExp || 0}
                              onChange={(e) =>
                                setFormData((prev) => ({
                                  ...prev,
                                  yearsOfExp: parseFloat(e.target.value),
                                }))
                              }
                              className="flex-1 h-2 bg-gray-200 dark:bg-neutral-700 rounded-lg appearance-none cursor-pointer accent-blue-500"
                            />
                            <span className="w-16 text-center px-3 py-2 bg-gray-100 dark:bg-neutral-800 rounded-lg text-gray-900 dark:text-white font-medium">
                              {formData.yearsOfExp || 0}y
                            </span>
                          </div>
                        </div>
                      </div>

                      {/* Footer */}
                      <div className="flex items-center justify-end gap-3 p-5 border-t border-gray-200/50 dark:border-neutral-800/50 bg-gray-50/50 dark:bg-neutral-800/30">
                        <Button
                          type="button"
                          onClick={handleClose}
                          className="px-5 py-2.5 bg-gray-200 dark:bg-neutral-700 hover:bg-gray-300 dark:hover:bg-neutral-600 text-gray-700 dark:text-neutral-300 rounded-xl transition-colors"
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          disabled={saving || !formData.skillName.trim()}
                          className="px-6 py-2.5 bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white rounded-xl shadow-lg shadow-blue-500/25 disabled:opacity-50 disabled:cursor-not-allowed transition-all"
                        >
                          {saving ? (
                            <>
                              <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                              Adding...
                            </>
                          ) : (
                            <>
                              <Plus className="w-4 h-4 mr-2" />
                              Add Skill
                            </>
                          )}
                        </Button>
                      </div>
                    </form>
                  </div>
                </motion.div>
              </Dialog.Content>
            </>
          )}
        </AnimatePresence>
      </Dialog.Portal>
    </Dialog.Root>
  );
}
